import * as THREE from "three";

import { BrowserRouter, Route, Switch, Redirect, Link } from "react-router-dom";

import React, { Suspense, useRef, useState, useMemo, useEffect } from "react";

import { useLoader, Canvas, useFrame, useThree } from "@react-three/fiber";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";

import { TextureLoader } from "three/src/loaders/TextureLoader";

import {
  OrbitControls,
  ContactShadows,
  Environment,
  Plane,
  useGLTF,
  useAnimations,
  AdaptiveDpr,
  Merged,
} from "@react-three/drei";
import BASEPATH from "../../basepath";
import { Vector3 } from "three";
import Particle from "./Particle";

function Particles({
  url = BASEPATH + "/shards.glb",
  radius = 1,
  randomness = 0.08,
}) {
  //const three = useThree();
  //const { size, viewport, gl, camera, clock } = three;

  //const model = useRef();

  //const dummy = useMemo(() => new THREE.Object3D(), []);

  const { scene, nodes, materials, animations } = useGLTF(url);

  const { ref, mixer, names, actions, clips } = useAnimations(
    animations,
    scene
  );

  const meshes = Object.values(nodes).filter((item) => item.type == "Mesh");
  meshes.forEach((item) => (item.position.x = 0));
  console.log("meshes", meshes);

  return (
    <group scale={[1, 1, 1]} position={[0, 0, 0]}>
      {meshes.map((mesh) => (
        // <primitive object={mesh} key={mesh.name} />
        <Particle
          model={mesh}
          radius={radius}
          randomness={randomness}
          key={mesh.name}
        />
      ))}
      {/* <instancedMesh
        ref={model}
        args={[
          parts[0].geometry,
          new THREE.MeshPhysicalMaterial({
            roughness: parts[0].material.roughness,
          }),
          parts.length,
        ]}
        castShadow
      ></instancedMesh> */}
    </group>
  );
}

export default Particles;
