import * as THREE from "three";

import { BrowserRouter, Route, Switch, Redirect, Link } from "react-router-dom";

import React, { Suspense, useRef, useState, useMemo, useEffect } from "react";

import { useLoader, Canvas, useFrame, useThree } from "@react-three/fiber";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";

import { TextureLoader } from "three/src/loaders/TextureLoader";

import {
  OrbitControls,
  ContactShadows,
  Environment,
  Plane,
  useGLTF,
  useAnimations,
  AdaptiveDpr,
  Merged,
} from "@react-three/drei";
import BASEPATH from "../../basepath";
import { Vector3 } from "three";

function Particle({
  model,
  radius = 1,
  randomness = 0.08,
  mouse = { current: [0, 0] },
  count = 100,
}) {
  const mesh = useRef();
  const light = useRef();
  const { size, viewport } = useThree();
  const aspect = size.width / viewport.width;

  const dummy = useMemo(() => new THREE.Object3D(), []);
  // Generate some random positions, speed factors and timings
  const particles = useMemo(() => {
    const temp = [];
    for (let i = 0; i < count; i++) {
      const t = Math.random() * 100;
      const factor = 20 + Math.random() * 100;
      const speed = 0.001 + Math.random() / 1000;
      const xFactor = -1 + Math.random() * 2;
      const yFactor = -1 + Math.random() * 2;
      const zFactor = -1 + Math.random() * 2;
      temp.push({ t, factor, speed, xFactor, yFactor, zFactor, mx: 0, my: 0 });
    }
    return temp;
  }, [count]);
  // The innards of this hook will run every frame
  useFrame((state) => {
    // Makes the light follow the mouse
    /*
    light.current.position.set(
      mouse.current[0] / aspect,
      -mouse.current[1] / aspect,
      0
    );*/
    // Run through the randomized data to calculate some movement
    particles.forEach((particle, i) => {
      let { t, factor, speed, xFactor, yFactor, zFactor } = particle;
      // There is no sense or reason to any of this, just messing around with trigonometric functions
      const s = (0.01 + Math.pow(Math.cos(t), 2)) * 1.1;
      t = particle.t += speed / 2 / s / 5;
      const a = Math.cos(t) + Math.sin(t * 1) / 10;
      const b = Math.sin(t) + Math.cos(t * 2) / 10;
      particle.mx += (mouse.current[0] - particle.mx) * 0.01;
      particle.my += (mouse.current[1] * -1 - particle.my) * 0.01;
      // Update the dummy object
      //console.log(state.clock);
      const fx =
        (particle.mx / 10) * a +
        xFactor +
        Math.cos((t / 10) * factor) +
        (Math.sin(t * 1) * factor) / 10;
      const fy =
        (particle.my / 10) * b +
        yFactor +
        Math.sin((t / 10) * factor) +
        (Math.cos(t * 2) * factor) / 10;

      const fz =
        (particle.my / 10) * b +
        zFactor +
        Math.cos((t / 10) * factor) +
        (Math.sin(t * 3) * factor) / 10;

      dummy.position.set(
        radius * Math.cos(fx) * Math.sin(fy),
        radius * Math.cos(fy),
        radius * Math.sin(fx) * Math.sin(fy)
      );
      dummy.scale.set(s, s, s);
      dummy.rotation.set(fx / 20, fz, fy / 20);
      dummy.updateMatrix();
      // And apply the matrix to the instanced item
      mesh.current.setMatrixAt(i, dummy.matrix);
    });
    mesh.current.instanceMatrix.needsUpdate = true;
  });

  return (
    <instancedMesh
      ref={mesh}
      args={[model.geometry, model.material, count]}
    ></instancedMesh>
  );
}

export default Particle;
